/**
 *
 * 从lodash里提炼一些常用的函数
 *
 * https://lodash.com/
 *
 * Created by lvjian on 2018/9/10.
 */
import merge from 'lodash/merge'
import uniqueId from 'lodash/uniqueId'
import arrayfill from 'lodash/fill'
import findIndex from 'lodash/findIndex'
import sortBy from 'lodash/sortBy'
import find from 'lodash/find'
import pullAt from 'lodash/pullAt'
import omit from 'lodash/omit'
import pick from 'lodash/pick'
import isUndefined from 'lodash/isUndefined'
import isNull from 'lodash/isNull'
import isNaN  from 'lodash/isNaN'

import isString from 'lodash/isString'
import isNumber from 'lodash/isNumber'
import isDate from 'lodash/isDate'

import isFunction from 'lodash/isFunction'
import isArray from 'lodash/isArray'
import extend from 'lodash/extend'
import keys from 'lodash/keys'
import values from 'lodash/values'
import groupBy from 'lodash/groupBy'

import toNumber from 'lodash/toNumber'
import toString from 'lodash/toString'

import repeat from 'lodash/repeat'
import replace from 'lodash/replace'
import reverse from 'lodash/reverse'

/*
* 添加两个数字
* */
import add from 'lodash/add'   //加
/*
* 计算number四舍五入到precision。
* */
import round from 'lodash/round'
import sum from 'lodash/sum'        //总和
import divide from 'lodash/divide'        //除
import subtract from 'lodash/subtract'    //减
import multiply from 'lodash/multiply'    //乘

const range = (s, e) => {
  const arr = []
  for (let i = s; i <= e; ++i) {
    arr.push(i)
  }
  return arr
}

const isEmpty = (val) => {
  if (val instanceof Array) {
    if (val.length === 0) return true
  } else if (val instanceof Object) {
    if (JSON.stringify(val) === '{}') return true
  } else {
    if (val === 'null' || isNull(val) || val === 'undefined' || isUndefined(val) || val === '') {
      return true
    }
  }
  return false
}

export default {
  extend,
  merge,
  keys,
  values,
  arrayfill,

  findIndex,
  // 创建一个元素数组。 以 iteratee 处理的结果升序排序。
  // 这个方法执行稳定排序，也就是说相同元素会保持原始排序。
  // iteratees 调用1个参数： (value)。
  //
  sortBy,

  find,
  // 根据索引 indexes，移除array中对应的元素，并返回被移除元素的数组
  pullAt,

  // 随机生成id
  uniqueId,

  range,

  omit,
  pick,

  isUndefined,
  isNull,
  isString,
  isNumber,
  isNaN,
  isDate,

  isEmpty,
  isFunction,
  isArray,
  groupBy,

  toNumber,
  toString,

  repeat,
  replace,
  round,
  reverse,
  add,
  sum,
  divide,
  subtract,
  multiply,
  deepCopy(src) {
    return JSON.parse(JSON.stringify(src))
  }

}
