/**
 * 数字四舍五入n位有效小数
 */
Vue.filter('roundNumber',function(value,n){
  // if(!/^[0-9]+$/.test(value)){
  //   return 0
  // }
  let result=Number(value).toFixed(n)
  if(isNaN(result)){
    return '0'
  }
  return result
});

/**
 * 对象判空
 */
Vue.filter('Emptiness',function(value,n){
	if(isNaN(result)){
		return
	  }
	let result=Number(value).toFixed(n)
	return result
  });

Vue.filter('subDate',function(value){
	if(value == null){
		return;
	}
	var result=value.substring(0,10)
	return result
});
/**
 * 时间戳转10 May 2018这种格式
 */
Vue.filter('ddMMMyyyy',function(value){
  let date = new Date(parseInt(value))
  let month = [
    'Jan','Feb','Mar','Apr','May','June','July','Aug','Sep','Oct','Nov','Dec'
  ];
  let y = date.getFullYear();
  let m = date.getMonth();
  m = month[parseInt(m)]
  let d = date.getDate();
  d = d < 10 ? ('0' + d) : d;
  return d+' '+m+' '+y
});
