/**
 * 首字母转大写
 */
Vue.filter('fToUp',function(str){
  if(!str){
    return;
  }
  if(str.indexOf('CRUDEOIL')!=-1){
    str = str.replace('CRUDEOIL', 'CRUDE OIL');
  }
  str = str.toLowerCase();
  let reg = /\b(\w)|\s(\w)/g; //  \b判断边界\s判断空格
  return str.replace(reg,function(m) {
    return m.toUpperCase()
  })
});

/**
 * 自动补0
 */
Vue.filter('supplement0', function(value) {
    if(value.toString().split('.')[1]&&value.toString().split('.')[1].length<=1){
        value+='0'
    }
    if(value.toString().split('.').length == 1){
        value+='.00'
    }
    return value
});
/**
 * 转换日期格式至时间戳
 */
Vue.filter('totimestamp', function(value) {
    return Date.parse(new Date(value))
});
/*
 * useage:金钱过滤器
 * {{123456 | moneyformat(',')}}  ------  123,456
 */
Vue.filter('moneyformat', function(num) {
  // console.log(num)
	if(num == null || num == undefined) {
		return num
	}
	if(isNaN(num)){
		return '0'
	  }
	if (typeof value == 'number') {
		value = num.toString()
	}
  num = num + '';
  if (!num.includes('.')) {
    num += '.'
  }
  // console.log( num.replace(/(\d)(?=(\d{3})+\.)/g, function($0, $1) {
  //   return $1 + ',';
  // }).replace(/\.$/, ''))

  return num.replace(/(\d)(?=(\d{3})+\.)/g, function($0, $1) {
    return $1 + ',';
  }).replace(/\.$/, '');
});
/**
 * 将数值四舍五入后格式化.
 * * {{123456 | formatNumber(2,0)}}  ------  123,456
 * @param num 数值(Number或者String)
 * @param cent 要保留的小数位(Number)
 * @param isThousand 是否需要千分位 0:不需要,1:需要(数值类型);
 * @return 格式的字符串,如'1,234,567.45'
 * @type String
 */
Vue.filter('formatNumber',function(num,cent,isThousand) {
  // console.log(num,cent,isThousand)
  var num = num.toString().replace(/\$|\,/g,'');

  // 检查传入数值为数值类型
  if(isNaN(num))
    num = "0";

  // 获取符号(正/负数)
  var sign = (num == (num = Math.abs(num)));

  num = Math.floor(num*Math.pow(10,cent)+0.50000000001); // 把指定的小数位先转换成整数.多余的小数位四舍五入
  var cents = num%Math.pow(10,cent);       // 求出小数位数值
  num = Math.floor(num/Math.pow(10,cent)).toString();  // 求出整数位数值
  cents = cents.toString();        // 把小数位转换成字符串,以便求小数位长度

  // 补足小数位到指定的位数
  while(cents.length<cent)
    cents = "0" + cents;

  if(isThousand) {
    // 对整数部分进行千分位格式化.
    for (var i = 0; i < Math.floor((num.length-(1+i))/3); i++)
      num = num.substring(0,num.length-(4*i+3))+','+ num.substring(num.length-(4*i+3));
  }

  if (cent > 0)
    return (((sign)?'':'-') + num + '.' + cents);
  else
    return (((sign)?'':'-') + num);
})
/*
 * useage:
 * {{ | nullformat}}  ------  -
 */
Vue.filter('nullformat', function(value, s) {
	let ret = value
	if (Object.isEmpty(ret)) {
		ret = !!s?s:'-'
	}
	return ret
});

Vue.filter('nullformat0', function(value, s) {
    let ret = value
    if (Object.isEmpty(ret)) {
        ret = !!s?s:'0'
    }
    return ret
});
/*
 * useage:数字转汉字
 * {{1 | n2cformat}}  ------ 一
 */
Vue.filter('n2cformat', function(value) {
    var chnNumChar = ["零","一","二","三","四","五","六","七","八","九"]
    var chnUnitSection = ["","万","亿","万亿","亿亿"]
    var chnUnitChar = ["","十","百","千"]
	//计算将数字转换成中文
    function SectionToChinese(section){
        var strIns = '', chnStr = '';
        var unitPos = 0;
        var zero = true;
        while(section > 0){
            var v = section % 10;
            if(v === 0){
                if(!zero){
                    zero = true;
                    chnStr = chnNumChar[v] + chnStr;
                }
            }else{
                zero = false;
                strIns = chnNumChar[v];
                strIns += chnUnitChar[unitPos];
                chnStr = strIns + chnStr;
            }
            unitPos++;
            section = Math.floor(section / 10);
        }
        return chnStr;
    }
    function NumberToChinese(num){
        var unitPos = 0;
        var strIns = '', chnStr = '';
        var needZero = false;

        if(num === 0){
            return chnNumChar[0];
        }

        while(num > 0){
            var section = num % 10000;
            if(needZero){
                chnStr = chnNumChar[0] + chnStr;
            }
            strIns = SectionToChinese(section);
            strIns += (section !== 0) ? chnUnitSection[unitPos] : chnUnitSection[0];
            chnStr = strIns + chnStr;
            needZero = (section < 1000) && (section > 0);
            num = Math.floor(num / 10000);
            unitPos++;
        }

        return chnStr;
    }
    return NumberToChinese(value)
});

/**
 * @desc 日期过滤器
 * @author hardy
 * @argument value
 * @argument type
 */
Vue.filter("fmtTime", function(value, type) {
	if (value == undefined || value == null) {
		return void 0
	}
	let dStr = Number.isNumber(value)?value:value.toString().split('-').join('/')
	let date = new Date(dStr);
	let y = date.getFullYear();
	let m = date.getMonth() + 1;
	m = m < 10 ? ('0' + m) : m;
	let d = date.getDate();
	d = d < 10 ? ('0' + d) : d;
	let h = date.getHours();
	h = h < 10 ? ('0' + h) : h;
	let minute = date.getMinutes();
	let second = date.getSeconds();
	minute = minute < 10 ? ('0' + minute) : minute;
	second = second < 10 ? ('0' + second) : second;
	return y + '-' + m + '-' + d + ' ' + h + ':' + minute + ':' + second;
})
