

const formatData = {
/*
*
* 判断是否数字
* */

  isNumber(n) {
  if(!isNaN(parseFloat(n)) && isFinite(n)){
    return n
  }else{
    return ''
  }
},
	/*
	 * num:金钱过滤器
	 * n:小数位数
	 * moneyformat(123456.123455,3)  ------  123,456.123
	 */
	moneyformat(num, n) {
	  if(!/^(-?\d+)(\.\d+)?$/.test(num) || num == null || num == undefined){
        return num;
    }
		if(n){
			num = num.toFixed(n)
		}

		if (typeof num == 'number') {
			num = num.toString()
		}
	    num = num + '';
		if (!num.includes('.')) {
			num += '.'
		}
		num =  num.replace(/(\d)(?=(\d{3})+\.)/g, function($0, $1) {
			return $1 + ',';
		}).replace(/\.$/, '')
	return num;
	},

    //日期格式化
    formatDate(date0, fmt) {
        if (!date0) return '';
        if (typeof date0 === 'string') {
            date0 = date0.replace(/(:\d+)\.\d+$/, '$1'); //IE 不能转换2017-12-28 10:43:02.0
        }
        let date = new Date(date0);
        if (isNaN(date.getDay())) return date0 || ''
        if (!fmt) fmt = 'yyyy-MM-dd HH:mm:ss';
        if (/(y+)/.test(fmt)) {
            fmt = fmt.replace(RegExp.$1, (date.getFullYear() + '').substr(4 - RegExp.$1.length));
        }
        let o = {
            'M+': date.getMonth() + 1,
            'd+': date.getDate(),
            'h+': date.getHours(),
            'm+': date.getMinutes(),
            's+': date.getSeconds()
        };
        o['H+'] = o['h+'];
        for (let k in o) {
            if (new RegExp(`(${k})`).test(fmt)) {
                let str = o[k] + '';
                fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? str : formatData.padLeftZero(str));
            }
        }
        return fmt;
    },
    padLeftZero(str) {
        return ('00' + str).substr(str.length);
    },
    // 小数点
    fixNumber(value,n) {
      let result=Number(value).toFixed(n);
      if(isNaN(result)){
        return 0
      }
      return result
    },
    //将手机号中间变成****
    mdnHidden(mdn) {
        return mdn.replace(/(\d{3})\d{4}(\d+)/, '$1****$2')
	},
	subDate(date){
        if (date!=null && date.length>=10){
            return date.substring(0,10)
        } else {

            return date;
        }

	},
  /**
   * 过滤时间戳为年月日
   */
  fmtDate(value, type) {
  if (value == undefined || value == null) {
    return void 0
  }
  // let dStr = Number.isNumber(value)?value:value.toString().split('-').join('/')
  let date = new Date(value);
  let y = date.getFullYear();
  let m = date.getMonth() + 1;
  m = m < 10 ? ('0' + m) : m;
  let d = date.getDate();
  d = d < 10 ? ('0' + d) : d;
  return y + '/' + m + '/' + d ;
},


/**
* 获取当前时间
* */
getCurrentDate() {
  let date = new Date();
  let seperator1 = "-";
  let month = date.getMonth() + 1;
  let strDate = date.getDate();
  if (month >= 1 && month <= 9) {
    month = "0" + month;
  }
  if (strDate >= 0 && strDate <= 9) {
    strDate = "0" + strDate;
  }
  let currentdate = date.getFullYear() + seperator1 + month + seperator1 + strDate
  return currentdate;
},
  /**
   *小数点校验判断
   */
  decimalPoint(value,n){
    if(value==null){
     return false
    }
    let arr=value.split('.');
    if(arr.length===1){
      return true
    }else{
      if(arr[1].length>3){
        return false
      }else{
        return true

      }
    }
  }
};

export default formatData;
